import "./globals.css";
import { Inter, IBM_Plex_Mono } from "next/font/google";

// Performance Fonts: Swap display to avoid layout shift (CLS)
const inter = Inter({ subsets: ["latin"], variable: "--font-sans", display: "swap" });
const mono = IBM_Plex_Mono({ 
  subsets: ["latin"], 
  weight: ["400", "700"], 
  variable: "--font-mono",
  display: "swap" 
});

export const metadata = {
  metadataBase: new URL("https://abid-folio.vercel.app"),
  title: {
    default: "Abid Khan | Enterprise SEO Strategist & Performance Engineer",
    template: "%s | Abid Khan SEO"
  },
  description: "Enterprise SEO specialist focused on high-scale organic growth. Expert in Technical SEO audits, Next.js performance, site migrations, and AEO (Answer Engine Optimization).",
  keywords: ["Enterprise SEO", "Technical SEO Specialist India", "Next.js SEO Expert", "Site Migration Strategy", "Programmatic SEO", "Crawl Budget Optimization", "Core Web Vitals", "AEO Strategy"],
  
  // Advanced Crawler Instructions
  robots: {
    index: true,
    follow: true,
    nocache: false,
    googleBot: {
      index: true,
      follow: true,
      noimageindex: false,
      'max-video-preview': -1,
      'max-image-preview': 'large',
      'max-snippet': -1,
    },
  },

  // Knowledge Graph Signals
  alternates: {
    canonical: "/",
    languages: {
      "en-US": "/en-US",
    },
  },

  openGraph: {
    title: "Abid Khan | Enterprise SEO & Frontend Engineering",
    description: "Architecting search-dominant web experiences. 100/100 Core Web Vitals & Programmatic SEO scale.",
    url: "/",
    siteName: "Abid Khan Technical Portfolio",
    images: [
      {
        url: "/seo.webp",
        width: 1200,
        height: 630,
        alt: "Technical SEO Performance Dashboard - Abid Khan",
      },
    ],
    locale: "en_IN",
    type: "website",
  },

  twitter: {
    card: "summary_large_image",
    title: "Abid Khan | Enterprise SEO Strategist",
    description: "Smarter SEO. Faster Web. Scalable Growth.",
    creator: "@kaabid1557",
    images: ["/seo.webp"],
  },

  verification: {
    google: "ytfTCtnXBBd9JGVolRG9rVjjZ0nGgZEO0uEfnJQUFvY",
    other: {
      "msvalidate.01": "B406A713428734B7264507731D5DC69A",
    },
  },

  category: "technology",
};

export const viewport = {
  themeColor: "#050505",
  width: "device-width",
  initialScale: 1,
  maximumScale: 5, // Accessible zooming
  colorScheme: "dark",
};

export default function RootLayout({ children }) {
  // Advanced Entity-Relationship Schema (Knowledge Graph Optimization)
  const jsonLd = {
    "@context": "https://schema.org",
    "@graph": [
      {
        "@type": "Person",
        "@id": "https://abid-folio.vercel.app/#person",
        "name": "Abid Khan",
        "jobTitle": "Enterprise SEO Strategist",
        "url": "https://abid-folio.vercel.app",
        "image": "https://abid-folio.vercel.app/seo.webp",
        "description": "Specialist in Technical SEO and Performance Engineering.",
        "address": {
          "@type": "PostalAddress",
          "addressLocality": "Vasai",
          "addressRegion": "Maharashtra",
          "addressCountry": "IN"
        },
        "sameAs": [
          "https://www.linkedin.com/in/abid-khan-coder/",
          "https://github.com/00Abid",
          "https://x.com/kaabid1557"
        ],
        "knowsAbout": ["Technical SEO", "Next.js", "Crawl Budget", "AEO", "Web Performance"]
      },
      {
        "@type": "WebSite",
        "@id": "https://abid-folio.vercel.app/#website",
        "url": "https://abid-folio.vercel.app",
        "name": "Abid Khan SEO Portfolio",
        "publisher": { "@id": "https://abid-folio.vercel.app/#person" },
        "inLanguage": "en-IN"
      },
      {
        "@type": "WebPage",
        "@id": "https://abid-folio.vercel.app/#webpage",
        "url": "https://abid-folio.vercel.app",
        "name": "Abid Khan | Enterprise SEO Strategy & Technical Portfolio",
        "isPartOf": { "@id": "https://abid-folio.vercel.app/#website" },
        "primaryImageOfPage": { "@id": "https://abid-folio.vercel.app/#person" },
        "about": { "@id": "https://abid-folio.vercel.app/#person" },
        "description": "Enterprise SEO case studies and technical insights."
      },
      {
        "@type": "BreadcrumbList",
        "itemListElement": [
          { "@type": "ListItem", "position": 1, "name": "Home", "item": "https://abid-folio.vercel.app/" },
          { "@type": "ListItem", "position": 2, "name": "Projects", "item": "https://abid-folio.vercel.app/#projects" },
          { "@type": "ListItem", "position": 3, "name": "Skills", "item": "https://abid-folio.vercel.app/#skills" },
          { "@type": "ListItem", "position": 4, "name": "Blog", "item": "https://abid-folio.vercel.app/blog" }
        ]
      }
    ]
  };

  return (
    <html lang="en-IN" className="scroll-smooth">
      <head>
        <script
          type="application/ld+json"
          dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd) }}
        />
      </head>
      <body className={`${inter.variable} ${mono.variable} font-sans antialiased bg-[#050505] text-white selection:bg-cyan-500 selection:text-black`}>
        {children}
      </body>
    </html>
  );
}